.PhotosFilter .flex-container {
    background: var(--accent-base);
    display: flex;
    gap: 8px;
    padding-block: 16px;
}

.flex-item {
    padding: 8px;
    border: 2px solid var(--black-base);
    border-radius: 4px;

    text-transform: uppercase;
    letter-spacing: 2px;

    transition: all 250ms ease-in;
}

.flex-item:hover {
    color: var(--accent-base);
    background: var(--black-base);
    font-style: italic;
    font-weight: bolder;
}