.Videos {
    background: url("../../../public/images/bg/videos.avif") no-repeat center center;
    background-size: cover;
}

.videos-overlay {
    background: var(--white-overlay);
}

.videos-container {
    margin-top: 32px;
    display: grid;
    grid-template-columns: 1fr;
    gap: 32px;
}

.video-item {
    border-radius: 8px;
    overflow: hidden;
}

.video-item video {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@media (width > 625px) {
    .videos-container {
        grid-template-columns: repeat(2, 1fr);
    }
}